<script>
    export default {
        name: "user-view",
        data() {
            return {
                model : {
                    payment : {},
                    transaction : {},
                },
                loading: true
            };
        },
        methods : {
            init(){
                this.loading = true;
                window.axios.get(`${window.location.origin}/transactions/${this.$route.params.id}/view`).then((response) => {
                    this.model = response.data.body.model;
                }).finally(() => {
                    this.loading = false;
                });
            }
        },
        mounted() {
            this.init();
        }
    }
</script>
<template>
    <div :class="['dimmer' , loading ? 'active' : '']">
        <div class="loader"></div>
        <div class="dimmer-content">
            <div class="row">
                <div class="col-lg-12">
                    <div class="card ribbon-box ">
                        <div class="card-body">
                            <div class="d-flex align-items-center mb-3 flex-wrap">
                                <h4 class="header-title m-0 flex-fill">Transaction Information</h4>
                            </div>
                            <div v-if="model.payment" class="text-left">
                                <p>
                                    <strong class="mr-2">Payment  :</strong>
                                    <router-link class="badge badge-light p-2 m-1 font-12" :to="`/${model.p_type}/${model.payment_id}/view`">{{ model.p_type }} - {{ model.payment_id }} - {{ model.payment.state }}</router-link>
                                </p>
                            </div>
                            <div v-if="model.transaction"  class="text-left">
                                <p>
                                    <strong class="mr-2">Transaction Type  :</strong>
                                    <router-link class="badge badge-light p-2 m-1 font-12" :to="`/${model.t_type}/${model.transaction_id}/view`">{{ model.t_type }} - {{ model.transaction_id }} - {{ model.transaction.state }}</router-link>
                                </p>
                            </div>
                            <p>
                                <strong class="mr-2">Payment ID :</strong>
                                <span>{{ model.payment_id }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Amount :</strong>
                                <span>{{ model.amount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Currency :</strong>
                                <span>{{ model.currency }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Phone :</strong>
                                <span>{{ model.phone }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Email :</strong>
                                <span>{{ model.email }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Ref :</strong>
                                <span>{{ model.ref }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Payment Ref :</strong>
                                <span>{{ model.payment_ref }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">State :</strong>
                                <span>{{ model.state }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Created :</strong>
                                <span>{{ model.created_at }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Error :</strong>
                                <span>{{ model.error }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Payment Completed  :</strong>
                                <span>{{ model.payment_completed }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Channel :</strong>
                                <span>{{ model.channel }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Created :</strong>
                                <span>{{ model.created_at }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Last :</strong>
                                <span>{{ model.updated_at }}</span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
